body{
  padding: 0;
  margin: 0;
}

#menu{
  display: none;
}
header{

  color: black;
  padding: 20px;
  font-size: 20px;
  font-weight: bolder;
  text-align: center;
}
 article{
 
  margin: 0;
  padding: 0;
}
.header-icon{
  display: flex;
  background-color: yellow;
  /* border-bottom: 1px solid yellow; */
  justify-content: space-between;
  align-items: center;
  padding:  10px 90px;
  margin: 0;
 
}
.homepage{
  position: relative;
  
}
nav a{
  text-decoration: none;
  font-size: 20px;
font-weight: bolder;
  color:  black;

}
nav li{
  padding: 6px 0;
}
/* .footer a{
  color: white;
  text-decoration: none;

} */
.App{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  background-color: rgba(0, 0, 0, 0.116);
}
.category-wraper{
  text-align: center;
  background-color: white;
  width: 240px;
  height: 320px;
  display: inline-block;
  margin:  10px 10px;
  padding-top: 10px;
  border-radius: 10%;
  border: 3px solid white;
  box-sizing: border-box;
  position: relative;
}
.product-image{
 
  width: 60%;
  padding: 0;
  margin: 0;
}
.product-title{
  color: black;
  padding: 0;
  margin: 0;
  font-size: 17px;
  padding: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  display: -webkit-box;
  -webkit-box-orient: vertical;

}
.price-wrapper{
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
}
.btn_wrapper{
  display: inline;
}
.price-wrapper>h4:first-of-type{
  color: chocolate;
}
.product-btn-wrapper>button{
  font-size: 15px;
  padding: 10px 5px;
  font-weight: 700;
  border-radius: 5px;
  margin: 0 5px;
  border: none;

}
.wrapper{
  display: flex;
 align-items: center;
}
.btn-wrapper{
 margin-left: 20px;

}

.product-btn-wrapper>button:first-of-type{
  
  background-color: yellow;
}
.product-btn-wrapper>button:hover{
  background-color: black;
  color: white;
  transition: 1s;
}
section{
  display: flex;
  height: 100%;
}

.basket-icon{
  position: relative;
 
}
.basket-icon p{
 
  background-color: red;
  /* border-radius: 50%; */
  padding: 1px 5px;
  font-size: 12px;
  border-radius: 10px;
  top: -14px;
  right: -5px;
  color: white;
  position: absolute;
  font-weight: bolder;
}
nav{
  width: 300px;
  max-height: fit-content;
  padding: 30px;
  margin: 0;
  background-color: white;
  border-right: 1px solid black;
}




article{

width: 100%;
}
.footer{
  background-color: black;
 padding: 20px;
  font-size: 20px;
  text-align: center;
 /* margin-top: 50px; */
  bottom: 0;
}
.footer a{
  color: white;
  margin-left: 80px;
  text-decoration: none;
  font-weight: 800;
}

.basket-wrapper{
  border-bottom: 1px solid black;
  
}


.basket-product-title a{
  text-decoration: none;
  font-size: 20px;
font-weight: 500;
  color:  black;
 
 
}


.basket-header{
 
  margin: 0;
  padding: 0;
 
 

}
.Product-value{
  padding-left: 20px;
 
}

.shopping-basket{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}
.checkout{
  padding-left: 30px;
  padding-right: 30px;
  margin-bottom: 20px;
}
.checkout > h4{
  padding-top: 40px;
}
.checkout > hr{
  border: 1px solid black;
}
.name-wrapper{

  display: flex;
  align-items: center;
  width: 45%;
}
.name-wrapper>p{
  padding-right: 20px;
}
#level{
  height: 80vh;
}
.Email-wrapper{
  
  display: flex;
  align-items: center;
  width: 45%;
}
.Email-wrapper>p{
  padding-right: 20px;
}
.Email-wrapper>input{
  width: 100%;
  padding: 5px 5px;
  outline: none;
}
.name-wrapper>input{
  width: 100%;
  padding: 5px 5px;
  outline: none;
}
.detail-wrapper{
  display: flex;
  justify-content: space-between;
  padding: 0 60px;
  margin-bottom: 30px;
}
.copyshipping-wrapper{
  display: flex;
}
.copyshipping-wrapper>p{
  padding-right: 10px;
}
.Address-wrapper{
  display: flex;
  justify-content: space-between;
  padding: 0 ;
  margin-bottom: 30px;
}
.Billing-wrapper{

 display: flex;
 margin: 0;
  width: 50%;
}
.Billing-wrapper > p{
 padding-right: 10px;
}
.input-wrapper{
  width: 65%;
  margin: 10px  0;
  
}
.input-wrapper>input{
  width: 100%;
  padding: 5px 5px;
  margin: 5px 0;
  outline: none;
}
.checkout-btn-wrapper{
  display: flex;
  justify-content: space-between;
  margin: 0 40px
  ;
}
.checkout-btn-wrapper > button{
  padding: 5px 20px;
font-size: 20px;
font-weight: 500;
}
.search{
 margin-left: -200px;
}
.search input{
  margin-left: 20px;
  height: 20px;
  width: 200px;
  text-decoration: none;
  outline: none;
}

#slider-text{
 
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color:  white;
  width: 400px;
  /* text-align: center; */
}
#home{
  display: flex;
  justify-content: space-between;
  padding: 50px 200px  ;
  background-color: black; 
  margin-bottom: 10px;
  height: 50vh;
 


}

#slider-text{
  
  margin-top: 49px;
  /* margin-bottom: 20px; */
}
#slider-text > h1{
margin: 0;
padding: 0;
font-weight: bolder;
font-size: 35px;
margin-bottom: 20px;
}

#slider-text > p{
  margin: 0;
padding: 0;
margin-bottom: 25px;
font-size: 29px;
}
#slider-text>h6{
  
  font-size: 10px;
}
#slider-text > h2{
  margin: 0;
padding: 0;
transition: 15s;
/* margin-top: 90px;
margin-top: 10px; */
margin-bottom: 30px;
}
#slider-text>h4{
  margin: 0;
  padding: 0;

  margin-bottom: 40px;;
}
#slider-text >button{
   margin: 0;
  padding: 0;
  height: 45px;
  width: 150px;
  border-radius: 10px;
  font-size: 15px;
  font-weight: bolder;
  background-color: yellow;
 
}
#slider-text >button:hover{
  background-color: white;
  color: black;
  transition: 1s;
}
#slider-img-wrapper{
  position: relative;
  width: 40%;
}
#slider-img-wrapper img{
  width: 100%;
  transition: 10s;
  

}
.order-wrapper{
  height: 61vh;
}

.order{
  text-align: center;
  position: relative;
 

}
.order>img{
  width: 290px;
}

.order button{
  padding: 10px 20px;
  font-size: 15px;
  margin-top: 10px;
  font-weight: bold;
  background-color: yellow;
  outline: none;
  border: none;
}

.order button:hover{
  background-color: black;
  color: white;
}
form{
  height: 76vh;
}
.order{
  position:relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
